/* ###### 8.1 Dashboard One  ###### #550a8a*/
body {
    background-color: #B0CFDE;
   }
  .az-content-dashboard {
    
    padding-top: 20px;
    @include media-breakpoint-up(lg) { padding-top: 30px; }
  }
  
  .az-dashboard-one-title {
    margin-bottom: 20px;
  
    @include media-breakpoint-up(sm) {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
  
    @include media-breakpoint-up(lg) { margin-bottom: 30px; }
  }
 
  .az-dashboard-title {
    font-size: 22px;
    font-weight: 700;
    color: $gray-900;
    letter-spacing: -1px;
    margin-bottom: 3px;
  }
  
  .az-dashboard-text {
    font-size: 13px;
    margin-bottom: 0;
  }
  
  .az-dashboard-date {
    display: flex;
    align-items: center;
    margin-top: 20px;
  
    @include media-breakpoint-up(sm) { margin-top: 0; }
  
    .date {
      display: flex;
  
      > div:first-child {
        font-size: 32px;
        font-family: Arial, sans-serif;
        font-weight: 500;
        color: $gray-900;
        line-height: .9;
  
        @include media-breakpoint-up(md) { font-size: 36px; }
      }
  
      > div:last-child {
        margin-left: 5px;
  
        span {
          display: block;
          color: $gray-900;
          font-weight: 700;
          font-size: 12px;
          line-height: 1.2;
          letter-spacing: -.5px;
  
          @include media-breakpoint-up(md) { font-size: 13px; }
  
          &:last-child {
            color: $gray-600;
            font-weight: 400;
          }
        }
      }
    }
  
    > i {
      font-size: 21px;
      line-height: 0;
      display: block;
      margin: 0 15px;
      color: $gray-500;
  
      @include media-breakpoint-up(md) {
        margin: 0 20px;
        font-size: 24px;
      }
    }
  }
  
  .az-dashboard-nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid $gray-300;
    margin-bottom: 20px;
  
    .nav-link {
      font-size: 12px;
      padding: 5px 10px 8px;
      display: flex;
      align-items: center;
      line-height: 1;
  
      @include media-breakpoint-up(sm) { font-size: $font-size-base; }
      @include media-breakpoint-up(lg) { padding: 5px 15px 10px; }
  
      @include hover-focus() { color: $az-color-primary; }
  
      + .nav-link { border-left: 1px solid $gray-300; }
    }
  
    .nav:first-child {
      .nav-link {
        color: $gray-900;
        display: none;
  
        @include media-breakpoint-up(sm) { display: block; }
  
        @include hover-focus() { color: $az-color-primary; }
  
        &.active { color: $az-color-primary; }
  
        &:first-child { padding-left: 0; }
  
        &:first-child,
        &:last-child {
          display: block;
        }
      }
    }
  
    .nav:last-child {
      .nav-link {
        color: $gray-700;
        display: none;
  
        @include media-breakpoint-up(md) { display: block; }
  
        @include hover-focus() { color: $az-color-primary; }
  
        i {
          font-size: 16px;
          margin-right: 7px;
          line-height: 0;
        }
  
        &:last-child {
          padding-right: 0;
          display: block;
  
          i { margin-right: 0; }
  
          @include media-breakpoint-up(md) { display: none; }
        }
      }
    }
  }

 

  












