  .card-dashboard-two {
    height: 200px;
    margin-top: -12px;
    width: 400px;
    margin-left: -162px;
    border-radius: 0;
    border-color: black;
    background-color: #B0CFDE;
  
   .production-txt{
    background-color: #000;
   }
  
    .card-header {
      background-color: transparent;
      padding: 20px 20px 0;
  
      h3 {
        background-color: #98AFC7;
        
      }
  
     
      h6 {
        font-size: 28px;
        font-weight: 600;
        font-family: 'Archivo', $font-family-base;
        color: $gray-900;
        display: flex;
        align-items: baseline;
        margin-bottom: 2px;
