  .card-dashboard-three {
    height: 150px;
    border-radius: 0;
    border-color: $gray-300;
    background-color: rgba(#fff,.5);
  
    @include media-breakpoint-up(md) { height: 170px; }
    @include media-breakpoint-up(lg) { height: 100%; }
  
    .card-header {
      background-color: transparent;
      padding: 20px;
      position: absolute;
      max-width: 50%;
  
      h6 {
        font-size: 28px;
        font-weight: 600;
        font-family: 'Archivo', $font-family-base;
        color: $gray-900;
        display: flex;
        align-items: baseline;
        margin-bottom: 15px;
        line-height: 1;
        letter-spacing: -1px;
  
        @include media-breakpoint-up(xl) { font-size: 32px; }
  
        small {
          font-size: 12px;
          font-weight: 400;
          font-family: $font-family-base;
          margin-left: 5px;
          letter-spacing: normal;
        }
      }
  
      p {
        margin-bottom: 10px;
        font-weight: 700;
        text-transform: uppercase;
        color: $gray-900;
      }
  
      > small {
        font-size: 11px;
        display: none;
        color: $gray-600;
  
        @include media-breakpoint-up(md) { display: block; }
      }
    }
  
    .card-body {
      padding: 0 0 0 20px;
      display: flex;
      justify-content: flex-end;
    }
  
    .chart {
      width: 70%;
      height: 150px;
  
      @media (min-width: 375px) { width: auto; }
  
      @include media-breakpoint-up(md) { height: 170px; }
      @include media-breakpoint-up(lg) {
        height: 182px;
        width: 80%;
      }
    }
  }
  
