  .card-table-one {
    height: 266px;
    width: 555px;
    margin-top: -95px;
    padding: 20px 20px 10px;
    border-color: $gray-300;
  
    .card-title {
      font-size: 14px;
      font-weight: 700;
      margin-bottom: 5px;
    }
  
    .table-responsive {
      width: calc(100vw - 80px);
      @include media-breakpoint-up(sm) { width: 100%; }
    }
  
    .table {
      margin-bottom: 0;
  
      thead,
      tbody {
        tr > th,
        tr > td {
          vertical-align: middle;
          white-space: nowrap;
          padding-right: 2px;
  
          @include media-breakpoint-up(md) {
            padding-left: 0;
            padding-right: 0;
          }
  
          &:nth-child(3),
          &:nth-child(4),
          &:nth-child(5) { text-align: right; }
  
          strong { color: $gray-900; }
        }
      }
    }
  
    .flag-icon {
      font-size: 20px;
      line-height: 1;
      @include border-radius(100%);
    }
  }
  
  .page-view-chart-wrapper {
    @include media-breakpoint-up(xl) { 
      height: 330px;
      margin-top: -56px;
    }
  }
  
  .sessions-chart-wrapper {
    @include media-breakpoint-up(xl) { 
      height: 180px;
      width: 80%;
    }
  }
  
  .acquisition-chart {
    canvas {
      @extend .w-50;
    }
  }
  
  .proBanner {
    position: fixed;
   // background-color: #ADD8E6;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 9999;
    .purchase-popup {
      background: #000;
      color: #fff;
      padding: 8px 20px;
      @include border-radius(3px);
  
      .btn {
        margin-right: 20px;
        font-weight: 500;
        color: $white;
        @include border-radius(5px);
        @include transition-duration(0.2s);
  
        &.download-button {
          background: rgba(249, 249, 249, 0.7);
          color: #969292;
          border: 1px solid darken(#e4e4e4,5%);
        }
  
        &.purchase-button {
          background-color: theme-color(info);
          color: $white;
          border: none;
          line-height: 1;
          vertical-align: middle;
        }
      }
  
      p {
        margin-bottom: auto;
        margin-top: auto;
        color: darken(#e4e4e4,20%);
        font-weight: 400;
        vertical-align: middle;
        line-height: 1;
      }
  
      i {
        vertical-align: middle;
        line-height: 1;
        margin: auto 0;
        color: darken(#e4e4e4,20%);
      }
      .bannerClose {
        cursor: pointer;
      }
    }
    &.hide {
      display: none;
    }
  }
